@font-face {
  font-family: Avenir;
  src: url(./fonts/Avenir-Book.ttf);
  font-style: normal;
  font-weight: 300;
}

@font-face {
  font-family: Avenir;
  src: url(./fonts/Avenir-Heavy.ttf);
  font-style: normal;
  font-weight: 800;
}

html {
  font-family: Avenir, sans-serif;
  background-color: #fff;
}

.searchForm {
  position: relative;
  width: 100%;
  margin-top: 20px;
  height: 70px;
}

.searchForm--small {
  height: 42px;
}

.icon {
  position: absolute;
  width: 19px;
  left: 26px;
  top: 25px;
}

.icon--small {
  position: absolute;
  width: 11px;
  left: 13px;
  top: 15px;
}

.input {
  box-sizing: border-box;
  padding-left: 62px;
  width: 100%;
  height: 100%;
  background: #fff;
  border: 1px solid #e1e7ed;
  box-shadow: 0 1px 8px rgba(61, 78, 97, 0.1);
  border-radius: 4px;
  font-family: inherit;
  font-weight: 800;
  font-size: 16px;
  color: #3d4e61;
}

.input--small {
  padding-left: 33px;
  font-size: 14px;
}

.input:hover {
  box-shadow: 0 3px 8px rgba(61, 78, 97, 0.2);
}

.input:focus {
  outline: none;
  background: linear-gradient(180deg, #fff 0%, f6f6f7 100%);
  border: 1px solid #e1e7ed;
  border-radius: 3px;
}

::placeholder {
  font-family: inherit;
  color: #3d4e61;
  font-weight: 300;
}
